import API from 'vue-lsi-util/APIAcceso'

const choferes= {
  namespaced: true,

    state: {
        choferActual: {loggeado: false, nombre: '', numero: ''},
    },

    mutations: {
        actualizar(state, payload) {
            state.choferActual.loggeado=payload.loggeado
            state.choferActual.numero=payload.numero
            state.choferActual.nombre=payload.nombre
        },
    },
  
    actions: {
        actualizar(state, payload) {
            this.commit('choferes/actualizar', payload)
            if (payload.loggeado) {
                sessionStorage.choferNumero=payload.numero
                sessionStorage.choferNombre=payload.nombre
            } else {
                sessionStorage.removeItem("choferNumero")
                sessionStorage.removeItem("choferNombre")
            }
        },

        async registrarEntregasItineradas(NumeroChofer, Fecha, Ventana, Detalle) {
            return new Promise (
                function (resolve, reject) {
                    const Cabeceras={NumeroChofer, Fecha, Ventana, Detalle}
                    console.log(Cabeceras)
                    API.actions.acceder({Ruta: 'choferes/setEntregasItineradas', 
                                        Cabeceras,
                                        Metodo: "Put",
                                        Cartel: "Registrando itinerancias ..."})
                    .then(datosLeidos => {
                        if (datosLeidos.Estado=="OK") {
                            resolve(datosLeidos.Datos)
                        } else {
                            reject(datosLeidos)
                        }
                    })
                    .catch(puteada => { 
                        reject(puteada) 
                    })
                }
            )            
        },

        async getEntregas(NumeroChofer, Fecha, Ventana) {
            return new Promise (
                function (resolve, reject) {
                    API.actions.acceder({Ruta: 'Choferes/getEntregas', 
                                        Cabeceras: {NumeroChofer, Fecha, Ventana},
                                        Cartel: "Obteniendo entregas ..."})
                    .then(datosLeidos => {
                        if (datosLeidos.Estado=="OK") {
                            resolve(datosLeidos.Datos)
                        } else {
                            reject(datosLeidos.Detalle)
                        }
                    })
                    .catch(puteada => { reject(puteada) })
                }
            )            
        },

        async getByNumero(Numero) {
            return new Promise (
                function (resolve, reject) {
                    API.actions.acceder({Ruta: 'Choferes/getByNumero', 
                                        Cabeceras: {Numero},
                                        Cartel: "Obteniendo chofer ..."})
                    .then(datosLeidos => {
                        if (datosLeidos.Estado=="OK") {
                            resolve(datosLeidos.Datos)
                        } else {
                            reject(datosLeidos.Detalle)
                        }
                    })
                    .catch(puteada => { reject(puteada) })
                }
            )            
        },
    },
}

export default choferes