import store from '../store'
import axios from "axios"
import router from '../router'

const usuarios = {
    namespaced: true,
    state: {
        usuarioActual: {Loggeado: false, Nombre: ''},
        API: null,
    },
    mutations: {
        actualizar(state, payload) {
            state.usuarioActual=payload
        },
        desloggear(state) {
            state.usuarioActual={Loggeado: false, Nombre: ''}
        },
        loggear(state, payload) {
            state.usuarioActual={Loggeado:true, Nombre: payload}
        },
    },
    actions: {
        actualizar(state, payload) {
            this.commit('usuarios/actualizar', payload)
        },
        desloggear(state) {
            this.commit('usuarios/desloggear')
            sessionStorage.removeItem("usuarioLoggeado")
            sessionStorage.removeItem("usuarioNombre")
            router.push("/")
        },
        async intentarLoggearse(state, payload) {
            store.dispatch('loading/mostrar', {titulo: 'Verificando credenciales'})

            const cabeceraDatos = {'Username': payload.Username, 'Password': payload.Password}

            const APICredenciales=store.state.usuarios.API.Credenciales
            await axios.get('usuarios/getByUsernameAndPassword', {auth: APICredenciales, headers: cabeceraDatos})
                .then (datos =>  {
                    if (datos.data.Estado=="OK") {
                        this.commit('usuarios/loggear', payload.Username)
                        sessionStorage.usuarioLoggeado=true
                        sessionStorage.usuarioNombre=payload.Username
                        router.push("/")
                    } else {
                        store.dispatch('snackbar/mostrar', datos.data.Detalle)
                    }
                })
                .catch(datos => {
                    console.log('Error en Axios')
                })
            store.dispatch('loading/ocultar')
        }
    }
}

export default usuarios